
.wrapper.fixed_layout .main-header {
  position: fixed;
  width: 100%;
}
.wrapper.fixed_layout .content-wrapper {
  padding-top: 50px;
}
.wrapper.fixed_layout .main-sidebar {
  position: fixed;
  height: 100vh;
}
@media (max-width: 767px) {
.wrapper.hide_logo .main-header .logo {
    display: none;
}
}
.logo-mini,
.logo-lg {
  text-align: left;
}
.logo-mini img,
  .logo-lg img {
    padding: .4em !important;
}
.logo-lg img {
  display: -webkit-inline-box;
  width: 25%;
}
.user-panel {
  height: 4em;
}
hr.visible-xs-block {
  width: 100%;
  background-color: rgba(0, 0, 0, 0.17);
  height: 1px;
  border-color: transparent;
}
.map-navbar {
  height: 50px;
  background-color: #0d6aad;
  color: #ffffff;
  font-size: 20px;
  position: relative;
  width: 100%;
  /*display: inline-block;*/
}
.m-navbar-rock,
.m-navbar-wall {
  height: 100%;
  width: 20em;
  left: 40px;
  padding-left: 30px;
  padding-top: 10px;
  padding-bottom: 10px;
  display: inline-block;
  position: relative;
  /*float: left;*/
}
.m-navbar-rock {
  border-right: solid 0.5px #0d5188;
}
.m-navbar-wall span {
  display: inline-block;
  height: 30px;
}
.btn-map-navbar-l,
.btn-map-navbar-r {
  height: 50px;
  width: 40px;
  position: relative;
  display: inline-block;
}

/*.btn-map-navbar-l{*/
/*left: 0;*/
/*}*/
/*.btn-map-navbar-r{*/
/*right: 0;*/
/*}*/
.m-navbar-l,
.m-navbar-r {
  display: inline-block;
  height: 100%;
  width: auto;
  position: absolute;
}
.m-navbar-l {
  left: 0;
}
.m-navbar-r {
  right: 0;
}



